import { fastify, type FastifyInstance } from "fastify";
import {
  getDefaultKnexPluginParams,
  startTestEnv,
  stopTestEnv,
} from "../../../helper";
import fp from "fastify-plugin";

let app: FastifyInstance;
let knex = getDefaultKnexPluginParams();

const knexPlugin = fp(async (fastify: any, options: any) => {
  fastify.decorate("knex", knex);
});

jest.mock("../../../../src/plugins/knex-plugin", () => {
  return knexPlugin;
});

beforeAll(async () => {
  app = await startTestEnv();
});

beforeEach(async () => {
  jest.resetAllMocks();
});

afterAll(async () => {
  await stopTestEnv();
});

describe("GET /public/exchange-rates/:currency", () => {
  it("should response exchange rates by currency", async () => {
    const data = [
      {
        source_currency: "EUR",
        target_currency: "RSD",
        source_rate: "1",
        target_rate: "117.201880",
        exchange_date: "2023-01-22",
      },
      {
        source_currency: "EUR",
        target_currency: "RSD",
        source_rate: "1",
        target_rate: "116.201880",
        exchange_date: "2023-01-21",
      },
      {
        source_currency: "EUR",
        target_currency: "RSD",
        source_rate: "1",
        target_rate: "115.201880",
        exchange_date: "2023-01-20",
      },
      {
        source_currency: "EUR",
        target_currency: "JPY",
        source_rate: "1",
        target_rate: "120",
        exchange_date: "2023-01-20",
      },
    ];

    knex.table = () => ({
      select: () => data,
    });

    const exchageRates = await app.inject({
      method: "GET",
      url: "/public/exchange-rates/RSD",
    });

    console.log(exchageRates.body)
    expect(exchageRates.statusCode).toBe(200);
    // expect(JSON.parse(exchageRates.body)).toEqual({
    //   data: ["EUR", "USD", "JPY"],
    // });
  });
});
